<template>
<div id="app">
  <div v-if= "this.humbug.board.squares.length">
    Number of moves : <input type="number" v-model.number="humbug.nMoves" required>
    
    <table id="squares" class="table">
      <tr v-for="(line, i) in humbug.board.squares" v-bind:key="`line-${i}`" class="row">
        <td v-for="(square, j) in line" v-bind:key="`square-${i}-${j}`" class="column">
          <select v-model="square.type">
            <option v-for="type in squareTypes"
                    :name="type"
                    :key="type"
                    >{{type}}</option>
            <option name=""></option>
          </select>
        </td>
      </tr>
    </table>
    
    <button type="button" @click="reset">Reset number of rows and colyumns</button>
  </div>
  
  <div v-if="this.humbug.board.squares.length == 0">
    Number of rows : <input type="number" v-model="nRows">
    Number of columns : <input type="number" v-model="nCols">
    
    <button type="button" @click="setSize">Define number of rows and columns</button>
  </div>
  <div>
    <button @click="copyToClipboard">Copy to clipboard</button>

    <div><textarea id="vjp" v-model="javaJson">
    </textarea></div>
  </div>
</div>
</template>

<script>
export default {
    name: 'App',
    methods: {
        copyToClipboard() {
            document.getElementById("vjp").select();
            document.execCommand('copy');
        },
        reset() {
            if (confirm("Remove everything and start again?"))
                this.humbug.board.squares = [];
        },
        setSize() {
            let squares= [];
            for (let i = 0; i < this.nRows; i++) {
                let line = [];
                for (let j = 0; j < this.nCols; j++) {
                    line.push({});
                }
                squares.push(line);
            }
            this.humbug.board.squares = squares;
        }
        
    },
    computed:  {
        javaJson() {
            let current = this.humbug;
            let newJson = {
                board: { squares: [] },
                animals: [],
                nMoves: current.nMoves
            };
            let iLine = 0;
            for (let line of current.board.squares) {
                let iCol = 0;
                let newLine = [];
                for (let square of line) {
                    let newSquare = null;
                    let curType = square.type;
                    switch (square.type) {
                    case 'GRASS':
                    case 'STAR':
                        newSquare = { type: curType };
                        break;
                    case '':
                    case undefined:
                        break;
                    default: // animal
                        newSquare = { type: "GRASS" };
                        newJson.animals.push({type: curType, positionOnBoard: { row: iLine, column: iCol } })
                        break;
                    }
                    newLine.push(newSquare);
                    iCol++;
                }
                newJson.board.squares.push(newLine);
                iLine++;
            }
            
            return JSON.stringify(newJson);
        }
    },
    data: function () {
        return {
            nRows: 4,
            nCols: 3,
            squareTypes: ["GRASS", "STAR", "Snail", "Grasshopper", "Spider", "Ladybird", "Bumbelbee", "Butterfly"],
            humbug: {
                "board": {
                    "squares": []
                }
            }
        }
        
    }
}
</script>

