    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
#vjp {
    border: 3px solid black;
    text-align: left;
}
textarea {
  width: 95%;
  height: 300px;
}
</style>
